import React, { forwardRef } from 'react';
import classNames from 'classnames';

import { Text } from '../../Typography';

import styles from './ComparisonCardOffer.module.scss';

export interface ComparisonCardOfferProps {
    type: string;
    title?: string;
    description?: string;
    infoHint?: React.ReactNode;
}

export const ComparisonCardOffer = forwardRef<
    HTMLDivElement,
    ComparisonCardOfferProps
>(({ type, title, description, infoHint }, ref) => {
    const classList = classNames({
        [styles['product']]: type === 'product',
        [styles['service']]: type === 'service',
    });

    return (
        <>
            {(title || description) && (
                <div className={`${styles.offer} ${classList}`} ref={ref}>
                    {title && (
                        <div className={styles.upper}>
                            <Text size="small" weight="600">
                                {title}
                            </Text>
                            {infoHint}
                        </div>
                    )}
                    {description && (
                        <Text
                            variant="line"
                            size="small"
                            className={styles.description}
                        >
                            {description}
                        </Text>
                    )}
                </div>
            )}
        </>
    );
});

ComparisonCardOffer.displayName = 'ComparisonCardOffer';
