import React, { ReactNode } from 'react';

import { Heading, Text } from '../../Typography';

import styles from './ComparisonCardPrice.module.scss';

export interface ComparisonCardPriceProps {
    primary?: boolean;
    primaryTitle?: string;
    primaryPrefix?: string;
    primaryPrice?: string;
    primarySuffix?: string;
    primaryDescription?: string;
    secondary?: boolean;
    secondaryTitle?: string;
    secondaryInfoHint?: React.ReactNode;
    secondaryPrefix?: string;
    secondaryPrice?: string;
    secondarySuffix?: string;
    secondaryDescription?: string;
    tertiary?: boolean;
    tertiaryTitle?: string;
    tertiaryInfoHint?: React.ReactNode;
    tertiaryPrefix?: string;
    tertiaryPrice?: string;
    tertiarySuffix?: string;
    tertiaryDescription?: string;
    children?: React.ReactNode;
    notificationTitle?: string;
    notificationDescription?: string;
    notificationInfoHint?: ReactNode;
}

export const ComparisonCardPrice: React.FC<ComparisonCardPriceProps> = ({
    primary,
    primaryTitle,
    primaryPrefix,
    primaryPrice,
    primarySuffix,
    primaryDescription,
    secondary,
    secondaryTitle,
    secondaryInfoHint,
    secondaryPrefix,
    secondaryPrice,
    secondarySuffix,
    secondaryDescription,
    tertiary,
    tertiaryTitle,
    tertiaryInfoHint,
    tertiaryPrefix,
    tertiaryPrice,
    tertiarySuffix,
    tertiaryDescription,
    children,
    notificationTitle,
    notificationDescription,
    notificationInfoHint,
}) => {
    return (
        <div className={styles.base}>
            {primary && (
                <div className={styles.price_section}>
                    <div className={styles.primary}>
                        {primaryTitle && (
                            <Text variant="line">{primaryTitle}</Text>
                        )}
                        {primaryPrice && (
                            <div className={styles.primary_value}>
                                {primaryPrefix && (
                                    <Text
                                        className={styles.primary_prefix}
                                        variant="line"
                                        weight="600"
                                    >
                                        {primaryPrefix}
                                    </Text>
                                )}
                                <Heading variant="h4" className={styles.price}>
                                    {primaryPrice}
                                </Heading>
                                {primarySuffix && (
                                    <Text
                                        className={styles.primary_suffix}
                                        variant="line"
                                        weight="600"
                                    >
                                        {primarySuffix}
                                    </Text>
                                )}
                            </div>
                        )}

                        {primaryDescription && (
                            <Text
                                variant="line"
                                {...(!primaryTitle && !primaryPrice
                                    ? { weight: '600' }
                                    : { size: 'small' })}
                                className={styles.description}
                            >
                                {primaryDescription}
                            </Text>
                        )}
                    </div>

                    {secondary && (
                        <div className={styles.secondary}>
                            <div className={styles.secondary_service}>
                                <div
                                    className={`${styles.secondary_value} ${
                                        secondaryPrefix || secondarySuffix
                                            ? styles.column
                                            : ''
                                    }`}
                                >
                                    <Text variant="line">{secondaryTitle}</Text>
                                    <div className={styles.secondary_price}>
                                        {secondaryPrefix && (
                                            <Text variant="line" weight="600">
                                                {secondaryPrefix}
                                            </Text>
                                        )}
                                        <Text
                                            className={styles.price}
                                            variant="line"
                                            weight="600"
                                        >
                                            {secondaryPrice}
                                        </Text>
                                        {secondarySuffix && (
                                            <Text variant="line" weight="600">
                                                {secondarySuffix}
                                            </Text>
                                        )}
                                    </div>
                                </div>
                                {secondaryInfoHint}
                            </div>

                            {secondaryDescription && (
                                <Text
                                    variant="line"
                                    size="small"
                                    className={styles.description}
                                >
                                    {secondaryDescription}
                                </Text>
                            )}
                        </div>
                    )}

                    {tertiary && (
                        <div className={styles.tertiary}>
                            <div className={styles.tertiary_service}>
                                <div
                                    className={`${styles.tertiary_value} ${
                                        tertiaryPrefix || tertiarySuffix
                                            ? styles.column
                                            : ''
                                    }`}
                                >
                                    <Text variant="line">{tertiaryTitle}</Text>
                                    <div className={styles.tertiary_price}>
                                        {tertiaryPrefix && (
                                            <Text variant="line" weight="600">
                                                {tertiaryPrefix}
                                            </Text>
                                        )}
                                        <Text
                                            className={styles.price}
                                            variant="line"
                                            weight="600"
                                        >
                                            {tertiaryPrice}
                                        </Text>
                                        {tertiarySuffix && (
                                            <Text variant="line" weight="600">
                                                {tertiarySuffix}
                                            </Text>
                                        )}
                                    </div>
                                </div>
                                {tertiaryInfoHint}
                            </div>

                            {tertiaryDescription && (
                                <Text
                                    variant="line"
                                    size="small"
                                    className={styles.description}
                                >
                                    {tertiaryDescription}
                                </Text>
                            )}
                        </div>
                    )}
                    {children && <div className={styles.link}>{children}</div>}
                </div>
            )}

            {(notificationTitle || notificationDescription) && (
                <div className={styles.notification}>
                    {notificationTitle && (
                        <div className={styles.upper}>
                            <Text
                                variant="line"
                                size="small"
                                weight="600"
                                className={styles.notification_title}
                            >
                                {notificationTitle}
                            </Text>
                            {notificationInfoHint}
                        </div>
                    )}

                    {notificationDescription && (
                        <Text
                            variant="line"
                            size="small"
                            className={styles.description}
                        >
                            {notificationDescription}
                        </Text>
                    )}
                </div>
            )}
        </div>
    );
};
