import React, { ReactNode } from 'react';
import classNames from 'classnames';

import type { Background } from '../../types';

import styles from './ComparisonCardDetails.module.scss';

export interface ComparisonCardDetailsProps {
    label?: string;
    children?: ReactNode;
    background?: Background;
}

export const ComparisonCardDetails: React.FC<ComparisonCardDetailsProps> = ({
    label,
    children,
    background = 'main',
}) => {
    const classList = classNames({
        [styles.base]: true,
        [styles['alternative']]: background === 'alternative',
    });
    return (
        <div className={classList} aria-label={label}>
            {children}
        </div>
    );
};
