import React, { ReactNode } from 'react';
import classNames from 'classnames';

import { Stack } from '../../Stack';
import type { Background } from '../../types';
import { Heading, Text } from '../../Typography';

import styles from './ComparisonCardHeader.module.scss';

export interface ComparisonCardHeaderProps {
    type: 'product' | 'service';
    icon?: ReactNode;
    overline?: string;
    title?: string;
    description?: string;
    children?: React.ReactNode;
    background?: Background;
}

export const ComparisonCardHeader: React.FC<ComparisonCardHeaderProps> = ({
    type,
    icon,
    overline,
    title,
    description,
    children,
    background = 'main',
}) => {
    const classList = classNames({
        [styles['header--product']]: type === 'product',
        [styles['header--service']]: type === 'service',
        [styles['alternative']]: background === 'alternative',
    });

    const iconWithClassname =
        icon &&
        React.cloneElement(icon as React.ReactElement, {
            className: styles.icon,
        });

    return (
        <div className={`${styles.header} ${classList}`}>
            {iconWithClassname}
            <div className={styles.content}>
                <Stack>
                    {overline && (
                        <Heading
                            uppercase
                            variant="subheadline"
                            className={styles.overline}
                        >
                            {overline}
                        </Heading>
                    )}
                    {title && (
                        <Heading variant="h5" className={styles.title}>
                            {title}
                        </Heading>
                    )}
                </Stack>

                {description && (
                    <Text
                        className={styles.description}
                        variant="line"
                        size="small"
                    >
                        {description}
                    </Text>
                )}

                {children}
            </div>
        </div>
    );
};
