import React from 'react';
import classNames from 'classnames';

import { Text } from '../../Typography';

import styles from './ComparisonCardParameter.module.scss';

export interface ComparisonCardParameterProps {
    type: string;
    title?: string;
    value?: string;
    infoHint?: React.ReactNode;
}

export const ComparisonCardParameter: React.FC<
    ComparisonCardParameterProps
> = ({ type, title, value, infoHint }) => {
    const baseClassName = classNames({
        [styles.base]: true,
        [styles.short]: type === 'short',
        [styles.long]: type === 'long',
    });

    return (
        <div className={baseClassName}>
            <Text
                variant="line"
                weight={type === 'long' ? '600' : undefined}
                className={styles.title}
            >
                {title}
            </Text>
            <div className={styles.container}>
                <Text
                    variant="line"
                    weight={type === 'short' ? '600' : undefined}
                    className={styles.value}
                >
                    {value}
                </Text>
                {infoHint}
            </div>
        </div>
    );
};
